<template>
	<div class="balance">
		<!--<div class="no_goods" v-if="list.length === 0">
	     暂无记录
	      	
	    </div>-->
		<div class="jifen-detail">		  	
		  	<ul>
		  		<li v-for="item in items">
		  			<div class="top">
		  				<div v-if="item.type == 1">晋级奖励</div>
		  				<div v-if="item.type == 2">佣金奖</div>
		  				<div v-if="item.type == 3">团队奖励</div>
		  				<div v-if="item.type == 4">联合创始人的薪酬佣金</div>
		  				<div v-if="item.type == 5">提现</div>
		  				<div>{{item.number}}</div>
		  			</div>
		  			<div class="top">
		  				<p>{{$Num.Totime(item.create_time)}}</p>
		  				<p v-if="item.state == 0">未审核</p>
		  				<p v-if="item.state == 1">成功</p>
		  				<p v-if="item.state == 2">驳回</p>
		  			</div>
		  		</li>		  		
		  	</ul>
		  </div>
		  <p class="tip">没有更多了哦~</p>
	</div>
</template>

<script>
export default{
	data(){
		return{
			items:[
			
			]
		}
	},
	methods:{
		getMoneyLog(page){
     	this.post('home/User/getMoneyLog',{page:page,number:20}).then(function(res){  	 		
			console.log(res)
			if(res.data.code===10000){
    			this.items=res.data.info.data;    		 			
    		}
			
    	}.bind(this))
     },
	},
	mounted(){
      this.getMoneyLog(1)
    }
}
</script>

<style>
.tip{text-align: center;margin-top: .2rem;color: #999;}
.jifen-detail{background: #fff;}
.jifen-detail .text{padding: .2rem;border-bottom:1px #E6E6E6 solid;}
.jifen-detail ul{padding: .2rem;padding-bottom: 0;}
.jifen-detail li{padding: .2rem 0;border-bottom: 1px #e6e6e6 solid;}
.jifen-detail li:last-child{border-bottom: 0;}
.jifen-detail p{color: #999;}
.jifen-detail .top{display: flex;flex-direction: row;justify-content: space-between;align-items: center;margin-bottom: .1rem;}
</style>


